.tinder-page-product {
  box-shadow: 0 3px 16px rgba(174, 191, 207, 0.3);
  border-radius: 18px;
}

.tinder-page-product-header {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 12px 0px 12px 20px;
}

.tinder-page-product-header .product-brand-view-name {
  font-size: 14px;
  color: #AEBFCF;
  padding-top: 4px;
}

.tinder-page-product-header .product-price_wrap {
  padding-top: 4px;
}

.tinder-page-product-header .product-price_new {
  font-weight: 500;
  margin-right: 4px;
}

.tinder-page-product-header .product-price_currency {
  display: contents;
  font-size: 10px;
  padding-right: 12px; 
}

.tinder-page-size-shop-wrap {
  display: flex;
  align-items: center;
  padding-top: 4px;
}

.tinder-page-product-header .product-size-block-wrap-small {
  padding: 0px 4px;
}

.tinder-page-product-header .product-size-block_content {
  font-size: 10px;
  padding: 2px 2px 1px 0px;
}

.tinder-page-product-header .product-count-shop-view {
  font-size: 10px;
  padding-left: 12px;
}

.tinder-page-product-image_wrap {
  display: flex;
}

.tinder-page-product-image {
  border-bottom-right-radius: 18px;
  border-bottom-left-radius: 18px;
  width: 100%; 
}

